// =====================================================
// HOOK: useAuth (Qun l Xc thc & Trng thi)
// =====================================================
const useAuth = () => {
  const [authState, setAuthState] = useState({
    authUser: null, // User object t Firebase Auth
    currentUser: null, // User data t Firestore
    role: 'student', // Vai tr (student, teacher, admin)
    isAuthReady: false, // Auth  sn sng ( check xong)
    isLoading: true, // ang ti data ngi dng t Firestore
    needsOnboarding: false, // Cn in thng tin
    kicked: false, // B  do ng nhp ni khc
    sessionConflict: null, // Pht hin xung t phin
  });

  const [localToken, setLocalToken] = useState(() => localStorage.getItem('sessionToken'));

  const handleSignOut = async () => {
    localStorage.removeItem('sessionToken');
    setLocalToken(null);
    await signOut(auth);
  };

  // 1. Lng nghe thay i trng thi Auth (ng nhp/ng xut)
  useEffect(() => {
    const unsubscribe = onAuthStateChanged(auth, async (user) => {
      if (user) {
        // Ngi dng  ng nhp
        const tokenResult = await user.getIdTokenResult(true); // Force refresh
        const role = tokenResult.claims.role || 'student';
        
        // Kim tra session conflict
        const userDocRef = doc(db, 'users', user.uid);
        const userDoc = await getDoc(userDocRef);

        if (userDoc.exists()) {
          const dbToken = userDoc.data().activeLoginToken;
          const currentLocalToken = localStorage.getItem('sessionToken');

          if (dbToken && dbToken !== currentLocalToken) {
            // Pht hin xung t!
            setAuthState(prev => ({
              ...prev,
              isAuthReady: true,
              isLoading: false,
              sessionConflict: { authUser: user, role: role }
            }));
            return; // Dng li, ch ngi dng xc nhn
          }
        }
        
        // Khng c xung t, tip tc ng nhp
        proceedToLogin(user, role);

      } else {
        // Ngi dng  ng xut
        setAuthState({
          authUser: null,
          currentUser: null,
          role: 'student',
          isAuthReady: true,
          isLoading: false,
          needsOnboarding: false,
          kicked: false,
          sessionConflict: null,
        });
        localStorage.removeItem('sessionToken');
        setLocalToken(null);
      }
    });

    return () => unsubscribe();
  }, []);

  // 2. Hm tip tc ng nhp (sau khi check conflict)
  const proceedToLogin = async (user, role) => {
    const newSessionToken = generateSessionToken();
    localStorage.setItem('sessionToken', newSessionToken);
    setLocalToken(newSessionToken);

    setAuthState(prev => ({
      ...prev,
      authUser: user,
      role: role,
      isAuthReady: true,
      isLoading: true, // Bt u ti data Firestore
      sessionConflict: null,
    }));

    // Cp nht token mi ln DB
    const userDocRef = doc(db, 'users', user.uid);
    try {
      await updateDoc(userDocRef, { activeLoginToken: newSessionToken }); // Dng mi
    } catch (error) {
      if (error.code !== 'not-found') {
        console.error("Li cp nht session token (cho user c):", error);
      }
    }
  };

  // 3. Lng nghe thay i ti liu ngi dng (Firestore)
  useEffect(() => {
    let unsubscribeUserDoc;

    if (authState.isAuthReady && authState.authUser) {
      const userDocRef = doc(db, 'users', authState.authUser.uid);
      
      unsubscribeUserDoc = onSnapshot(userDocRef, (docSnap) => {
        if (docSnap.exists()) {
          const userData = docSnap.data();
          
          // Kim tra b  (session management)
          const dbToken = userData.activeLoginToken;
          if (localToken && dbToken && dbToken !== localToken) {
            handleSignOut(); // ng xut thit b ny
            setAuthState(prev => ({ ...prev, kicked: true }));
            return;
          }

          setAuthState(prev => ({
            ...prev,
            currentUser: userData,
            isLoading: false,
            needsOnboarding: false, // User  tn ti, khng cn onboarding
          }));
        } else {
          // Ngi dng mi, cn onboarding
          setAuthState(prev => ({
            ...prev,
            currentUser: null,
            isLoading: false,
            needsOnboarding: true,
          }));
        }
      }, (error) => {
        console.error("Li lng nghe user document:", error);
        setAuthState(prev => ({ ...prev, isLoading: false }));
      });
    } else if (authState.isAuthReady && !authState.authUser) {
      //  sn sng nhng cha ng nhp
      setAuthState(prev => ({ ...prev, isLoading: false }));
    }

    return () => {
      if (unsubscribeUserDoc) {
        unsubscribeUserDoc();
      }
    };
  }, [authState.isAuthReady, authState.authUser, localToken]);
  
  // Hm cp nht needsOnboarding (cho OnboardingForm)
  const setOnboardingCompleted = () => {
    setAuthState(prev => ({ ...prev, needsOnboarding: false }));
  };

  return { 
    ...authState, 
    handleSignOut, 
    proceedToLogin,
    setOnboardingCompleted 
  };
};

// =====================================================
// HOOK: usePublicData (Ti d liu chung)
// =====================================================
const usePublicData = () => {
  const { isAuthReady, authUser } = useContext(AppContext);
  const [data, setData] = useState({
    subjects: [],
    courses: [],
    quizzes: [],
    loading: true,
    error: null,
  });

  useEffect(() => {
    if (!isAuthReady) return; // Ch chy khi Auth  sn sng

    if (!authUser) {
        setData(prev => ({ ...prev, loading: false }));
        return;
    }

    const fetchCollection = (collectionName, setError) => {
      const q = query(collection(db, collectionName));
      
      return onSnapshot(q, (querySnapshot) => {
        const items = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
        setData(prev => ({
          ...prev,
          [collectionName]: items,
        }));
      }, (err) => {
        console.error(`Error fetching ${collectionName}:`, err);
        setError(`Li ti ${collectionName}: ${err.message}`);
      });
    };

    const errors = [];
    // Ghi ch: Firestore Rules phi cho php user  auth c cc collection ny
    const unsubSubjects = fetchCollection('subjects', (e) => errors.push(e));
    const unsubCourses = fetchCollection('courses', (e) => errors.push(e));
    const unsubQuizzes = fetchCollection('quizzes', (e) => errors.push(e));

    setData(prev => ({
      ...prev,
      loading: false,
      error: errors.length > 0 ? errors.join(', ') : null,
    }));

    return () => {
      unsubSubjects();
      unsubCourses();
      unsubQuizzes();
    };
  }, [isAuthReady, authUser]);

  return data;
};

// =====================================================
// HOOK: useAdminData (Ti d liu cho Admin)
// =====================================================
const useAdminData = (role) => {
  const [adminData, setAdminData] = useState({
    users: [],
    transactions: [],
    orders: [],
    loading: true,
    error: null,
  });

  useEffect(() => {
    if (role !== 'admin') {
      setAdminData(prev => ({ ...prev, loading: false }));
      return; // Khng phi admin, khng ti
    }

    let usersLoaded = false;
    let transactionsLoaded = false;
    let ordersLoaded = false;
    const errors = [];

    const checkLoadingDone = () => {
      if (usersLoaded && transactionsLoaded && ordersLoaded) {
        setAdminData(prev => ({
          ...prev,
          loading: false,
          error: errors.length > 0 ? errors.join(', ') : null,
        }));
      }
    };

    // Ti Users
    const qUsers = query(collection(db, 'users'));
    const unsubUsers = onSnapshot(qUsers, (snapshot) => {
      const userList = snapshot.docs.map(doc => ({ uid: doc.id, ...doc.data() }));
      setAdminData(prev => ({ ...prev, users: userList }));
      usersLoaded = true;
      checkLoadingDone();
    }, (err) => {
      console.error("Li ti danh sch ngi dng:", err);
      errors.push("Li ti ngi dng");
      usersLoaded = true;
      checkLoadingDone();
    });

    // Ti Transactions
    const qTrans = query(collection(db, 'transactions'));
    const unsubTrans = onSnapshot(qTrans, (snapshot) => {
      const transList = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setAdminData(prev => ({ ...prev, transactions: transList }));
      transactionsLoaded = true;
      checkLoadingDone();
    }, (err) => {
      console.error("Li ti giao dch:", err);
      errors.push("Li ti giao dch");
      transactionsLoaded = true;
      checkLoadingDone();
    });

    // Ti Orders
    const qOrders = query(collection(db, 'orders'));
    const unsubOrders = onSnapshot(qOrders, (snapshot) => {
      const orderList = snapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      setAdminData(prev => ({ ...prev, orders: orderList }));
      ordersLoaded = true;
      checkLoadingDone();
    }, (err) => {
      console.error("Li ti n hng:", err);
      errors.push("Li ti n hng");
      ordersLoaded = true;
      checkLoadingDone();
    });

    return () => {
      unsubUsers();
      unsubTrans();
      unsubOrders();
    };
  }, [role]);

  return adminData;
};

