// Component Qun l Ngi dng
const UserManager = ({ users }) => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  
  //   CP NHT: grantRole (dng fetch)
  const grantRole = async (uid, role) => {
    setLoading(true);
    setError('');
    try {
      // 1. Ly token ca Admin
      const user = auth.currentUser;
      if (!user) throw new Error("Admin cha ng nhp");
      const token = await user.getIdToken();

      // 2. Gi API Vercel
      const response = await fetch(`${VERCEL_API_URL}/api/grantRole`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ uid: uid, role: role })
      });

      const result = await response.json();
      if (!response.ok) {
        throw new Error(result.message || "Li khng xc nh");
      }
      
      alert(`Thnh cng: ${result.message}`);
      // onSnapshot ca useAdminData s t cp nht UI

    } catch (err) {
      console.error(err);
      setError(err.message);
      alert(`Tht bi: ${err.message}`);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="bg-white rounded-2xl shadow-lg overflow-hidden">
      {error && <div className="p-4 bg-red-100 text-red-700">{error}</div>}
      <table className="w-full min-w-lg">
        <thead className="bg-gray-50">
          <tr>
            <th className="px-6 py-4 text-left text-xs font-bold text-gray-600 uppercase tracking-wider">H tn</th>
            <th className="px-6 py-4 text-left text-xs font-bold text-gray-600 uppercase tracking-wider">Email</th>
            <th className="px-6 py-4 text-left text-xs font-bold text-gray-600 uppercase tracking-wider">Vai tr</th>
            <th className="px-6 py-4 text-right text-xs font-bold text-gray-600 uppercase tracking-wider">Hnh ng</th>
          </tr>
        </thead>
        <tbody className="divide-y divide-gray-200">
          {users.map(user => (
            <tr key={user.uid}>
              <td className="px-6 py-4 whitespace-nowrap">
                <p className="font-semibold text-gray-800">{user.hoTen}</p>
                <p className="text-sm text-gray-500">Lp {user.lop}</p>
              </td>
              <td className="px-6 py-4 whitespace-nowrap">
                <p className="text-gray-700">{user.email}</p>
              </td>
              <td className="px-6 py-4 whitespace-nowrap">
                {user.role ? (
                  <span className={`px-3 py-1 rounded-full text-xs font-bold ${
                    user.role === 'admin' ? 'bg-red-100 text-red-700' : 'bg-teal-100 text-teal-700'
                  }`}>
                    {user.role}
                  </span>
                ) : (
                  <span className="text-gray-400 text-sm">student</span>
                )}
              </td>
              <td className="px-6 py-4 whitespace-nowrap text-right space-x-2">
                <button
                  onClick={() => grantRole(user.uid, 'teacher')}
                  disabled={loading || user.role === 'teacher'}
                  className="bg-teal-100 text-teal-700 px-3 py-2 rounded-lg hover:bg-teal-200 transition text-sm disabled:opacity-50"
                >
                  Cp quyn Teacher
                </button>
                <button
                  onClick={() => grantRole(user.uid, 'admin')}
                  disabled={loading || user.role === 'admin'}
                  className="bg-red-100 text-red-700 px-3 py-2 rounded-lg hover:bg-red-200 transition text-sm disabled:opacity-50"
                >
                  Cp quyn Admin
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

// Component Qun l Ni dung (Chung cho Subjects, Courses, Quizzes)
const ContentManager = ({ type, items, onSave, onDelete }) => {
  const [editingItem, setEditingItem] = useState(null); // null, 'new', hoc { id, ... }
  const [formData, setFormData] = useState({});
  const [formLoading, setFormLoading] = useState(false);
  
  const getEmptyForm = () => {
    switch (type) {
      case 'subjects': return { name: '', price: 0, quizIds: [] };
      case 'courses': return { name: '', price: 0, subjectIds: [], quizIds: [] };
      case 'quizzes': return { title: '', embedCode: '', createdBy: '' };
      default: return {};
    }
  };
  
  const handleEdit = (item) => {
    setEditingItem(item);
    setFormData(item);
  };
  
  const handleNew = () => {
    setEditingItem('new');
    setFormData(getEmptyForm());
  };
  
  const handleCancel = () => {
    setEditingItem(null);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    if (name === 'price') {
      setFormData(f => ({ ...f, [name]: Number(value) }));
    } else if (name === 'quizIds' || name === 'subjectIds') {
      setFormData(f => ({ ...f, [name]: value.split(',').map(s => s.trim()).filter(Boolean) }));
    } else {
      setFormData(f => ({ ...f, [name]: value }));
    }
  };
  
  const handleSubmit = async (e) => {
    e.preventDefault();
    setFormLoading(true);
    await onSave(formData, editingItem === 'new' ? null : editingItem.id);
    setFormLoading(false);
    setEditingItem(null);
  };

  const renderForm = () => (
    <div className="fixed inset-0 bg-black/50 z-40 flex items-center justify-center p-8">
      <div className="bg-white p-8 rounded-2xl shadow-xl w-full max-w-2xl max-h-[90vh] overflow-y-auto">
        <h3 className="text-2xl font-bold mb-6">
          {editingItem === 'new' ? 'To mi' : 'Chnh sa'}
        </h3>
        <form onSubmit={handleSubmit} className="space-y-4">
          {Object.keys(formData).map(key => {
            if (key === 'id' || key === 'createdBy' || key.includes('At')) return null;
            
            const label = key.charAt(0).toUpperCase() + key.slice(1);
            const value = formData[key];
            
            if (key === 'embedCode') {
              return (
                <div key={key}>
                  <label className="block text-sm font-bold text-gray-700 mb-2">{label}</label>
                  <textarea
                    name={key}
                    value={value}
                    onChange={handleChange}
                    rows={5}
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:border-blue-500 focus:outline-none font-mono text-sm"
                  />
                </div>
              );
            }
            
            if (Array.isArray(value)) {
              return (
                <div key={key}>
                  <label className="block text-sm font-bold text-gray-700 mb-2">{label} (cch nhau bng du phy)</label>
                  <input
                    type="text"
                    name={key}
                    value={value.join(', ')}
                    onChange={handleChange}
                    className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:border-blue-500 focus:outline-none"
                  />
                </div>
              );
            }
            
            return (
              <div key={key}>
                <label className="block text-sm font-bold text-gray-700 mb-2">{label}</label>
                <input
                  type={typeof value === 'number' ? 'number' : 'text'}
                  name={key}
                  value={value}
                  onChange={handleChange}
                  className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:border-blue-500 focus:outline-none"
                />
              </div>
            );
          })}
          
          <div className="flex justify-end gap-4 pt-6">
            <button
              type="button"
              onClick={handleCancel}
              disabled={formLoading}
              className="bg-gray-200 text-gray-800 font-semibold px-6 py-3 rounded-xl hover:bg-gray-300 transition"
            >
              Hy
            </button>
            <button
              type="submit"
              disabled={formLoading}
              className="bg-blue-600 text-white font-semibold px-6 py-3 rounded-xl hover:bg-blue-700 transition disabled:opacity-50"
            >
              {formLoading ? <Loader2 className="animate-spin" /> : 'Lu'}
            </button>
          </div>
        </form>
      </div>
    </div>
  );

  return (
    <div className="bg-white rounded-2xl shadow-lg overflow-hidden">
      {editingItem && renderForm()}
      <div className="p-6 flex justify-between items-center">
        <h3 className="text-xl font-bold">Qun l {type}</h3>
        <button
          onClick={handleNew}
          className="bg-blue-600 text-white font-bold px-5 py-2 rounded-xl hover:bg-blue-700 transition flex items-center gap-2"
        >
          <Plus size={20} /> To mi
        </button>
      </div>
      <table className="w-full min-w-lg">
        <thead className="bg-gray-50">
          <tr>
            <th className="px-6 py-4 text-left text-xs font-bold text-gray-600 uppercase tracking-wider">Tn / Tiu </th>
            <th className="px-6 py-4 text-left text-xs font-bold text-gray-600 uppercase tracking-wider">Thng tin</th>
            <th className="px-6 py-4 text-right text-xs font-bold text-gray-600 uppercase tracking-wider">Hnh ng</th>
          </tr>
        </thead>
        <tbody className="divide-y divide-gray-200">
          {items.map(item => (
            <tr key={item.id}>
              <td className="px-6 py-4 whitespace-nowrap">
                <p className="font-semibold text-gray-800">{item.name || item.title}</p>
                <p className="text-sm text-gray-500">{item.id}</p>
              </td>
              <td className="px-6 py-4">
                {item.price !== undefined && <p>Gi: {formatCurrency(item.price)}</p>}
                {item.quizIds && <p>S quiz: {item.quizIds.length}</p>}
                {item.subjectIds && <p>S mn: {item.subjectIds.length}</p>}
              </td>
              <td className="px-6 py-4 whitespace-nowrap text-right space-x-2">
                <button
                  onClick={() => handleEdit(item)}
                  className="bg-gray-100 text-gray-700 px-3 py-2 rounded-lg hover:bg-gray-200 transition text-sm"
                >
                  Sa
                </button>
                <button
                  onClick={() => onDelete(item.id)}
                  className="bg-red-100 text-red-700 px-3 py-2 rounded-lg hover:bg-red-200 transition text-sm"
                >
                  Xa
                </button>
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

// Component Cp Key Th cng
const ManualKeyGrant = ({ users, subjects, courses }) => {
  const [selectedUser, setSelectedUser] = useState('');
  const [selectedItem, setSelectedItem] = useState(''); // 'subject_xxx' or 'course_xxx'
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [message, setMessage] = useState('');

  //   CP NHT: handleSubmit (dng fetch)
  const handleSubmit = async (e) => {
    e.preventDefault();
    if (!selectedUser || !selectedItem) {
      setError("Vui lng chn ngi dng v vt phm.");
      return;
    }
    
    setLoading(true);
    setError('');
    setMessage('');
    
    try {
      // 1. Ly token ca Admin
      const user = auth.currentUser;
      if (!user) throw new Error("Admin cha ng nhp");
      const token = await user.getIdToken();
      
      const [type, itemId] = selectedItem.split('_');
      
      // 2. Gi API Vercel
      const response = await fetch(`${VERCEL_API_URL}/api/manualGrant`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify({ uid: selectedUser, type: type, itemId: itemId })
      });
      
      const result = await response.json();
      if (!response.ok) {
        throw new Error(result.message || "Li khng xc nh");
      }

      setMessage(result.message);
      
    } catch (err) {
      console.error(err);
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="bg-white rounded-2xl shadow-lg p-8">
      <h3 className="text-xl font-bold mb-6">Cp Key Trc tip (Manual Grant)</h3>
      {error && <div className="p-4 mb-4 bg-red-100 text-red-700 rounded-lg">{error}</div>}
      {message && <div className="p-4 mb-4 bg-green-100 text-green-700 rounded-lg">{message}</div>}
      
      <form onSubmit={handleSubmit} className="space-y-6">
        <div>
          <label className="block text-sm font-bold text-gray-700 mb-2">Chn Ngi dng</label>
          <select 
            value={selectedUser} 
            onChange={(e) => setSelectedUser(e.target.value)}
            className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:border-blue-500 focus:outline-none"
          >
            <option value="">-- Chn --</option>
            {users.map(u => (
              <option key={u.uid} value={u.uid}>{u.hoTen} ({u.email})</option>
            ))}
          </select>
        </div>
        
        <div>
          <label className="block text-sm font-bold text-gray-700 mb-2">Chn Vt phm (Mn hc / Kha hc)</label>
          <select 
            value={selectedItem} 
            onChange={(e) => setSelectedItem(e.target.value)}
            className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:border-blue-500 focus:outline-none"
          >
            <option value="">-- Chn --</option>
            <optgroup label="Mn hc">
              {subjects.map(s => (
                <option key={s.id} value={`subject_${s.id}`}>{s.name} ({formatCurrency(s.price)})</option>
              ))}
            </optgroup>
            <optgroup label="Kha hc">
              {courses.map(c => (
                <option key={c.id} value={`course_${c.id}`}>{c.name} ({formatCurrency(c.price)})</option>
              ))}
            </optgroup>
          </select>
        </div>
        
        <button
          type="submit"
          disabled={loading}
          className="w-full bg-gradient-to-r from-green-600 to-blue-600 text-white font-bold py-4 rounded-xl hover:shadow-xl transition disabled:opacity-50"
        >
          {loading ? <Loader2 className="animate-spin" /> : 'Cp quyn truy cp'}
        </button>
      </form>
    </div>
  );
};

//  MI: Component To Key Th cng (Linh hot)
const ManualKeyGenerator = ({ subjects, courses }) => {
  const [cart, setCart] = useState({ subjects: [], courses: [] });
  const [capability, setCapability] = useState(''); // 'TEACHER_QUIZ_CREATION'
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');
  const [message, setMessage] = useState(''); //  hin th Key  to

  //   CP NHT: handleSubmit (dng fetch)
  const handleSubmit = async (e) => {
    e.preventDefault();
    
    const isEmpty = cart.subjects.length === 0 && cart.courses.length === 0;
    if (isEmpty && !capability) {
      setError("Vui lng chn t nht mt vt phm hoc mt tnh nng  to Key.");
      return;
    }
    
    setLoading(true);
    setError('');
    setMessage('');
    
    try {
      // 1. Ly token ca Admin
      const user = auth.currentUser;
      if (!user) throw new Error("Admin cha ng nhp");
      const token = await user.getIdToken();
      
      const payload = {
        status: "new",
        ...(isEmpty ? { unlocksCapability: capability } : { cartToUnlock: cart })
      };
      
      // 2. Gi API Vercel
      const response = await fetch(`${VERCEL_API_URL}/api/createAccessKey`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(payload)
      });
      
      const result = await response.json();
      if (!response.ok) {
        throw new Error(result.message || "Li khng xc nh");
      }
      
      // Backend API tr v { success: true, key: '...' }
      setMessage(`To Key thnh cng: ${result.key}\n\n(Hy copy v gi cho ngi dng)`);
      setCart({ subjects: [], courses: [] }); // Reset
      setCapability('');
      
    } catch (err) {
      console.error(err);
      setError(err.message);
    } finally {
      setLoading(false);
    }
  };

  // Logic mini-cart  chn kha hc
  const addToCart = (type, id) => {
    if (type === 'subject') {
      if (!cart.subjects.includes(id)) setCart(c => ({ ...c, subjects: [...c.subjects, id] }));
    } else {
      if (!cart.courses.includes(id)) setCart(c => ({ ...c, courses: [...c.courses, id] }));
    }
  };
  const removeFromCart = (type, id) => {
    if (type === 'subject') {
      setCart(c => ({ ...c, subjects: c.subjects.filter(s => s !== id) }));
    } else {
      setCart(c => ({ ...c, courses: c.courses.filter(c => c !== id) }));
    }
  };

  return (
    <div className="bg-white rounded-2xl shadow-lg p-8">
      <h3 className="text-xl font-bold mb-6">To Key Th cng (Flexible Key)</h3>
      {error && <div className="p-4 mb-4 bg-red-100 text-red-700 rounded-lg">{error}</div>}
      {message && (
        <div className="p-4 mb-4 bg-green-100 text-green-700 rounded-lg">
          <pre className="whitespace-pre-wrap font-sans">{message}</pre>
        </div>
      )}
      
      <form onSubmit={handleSubmit} className="space-y-6">
        
        {/* Phn 1: Chn Tnh nng */}
        <div>
          <label className="block text-sm font-bold text-gray-700 mb-2">Chn Tnh nng (u tin cao hn)</label>
          <select 
            value={capability} 
            onChange={(e) => setCapability(e.target.value)}
            className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:border-blue-500 focus:outline-none"
          >
            <option value="">-- Khng chn (To key ni dung) --</option>
            <option value="TEACHER_QUIZ_CREATION">Cp quyn Gio vin (To Quiz)</option>
            {/* Thm cc quyn khc sau ny */}
          </select>
          <p className="text-xs text-gray-500 mt-1">Nu chn tnh nng, Key s b qua cc vt phm bn di.</p>
        </div>

        <div className="relative flex py-2 items-center">
          <div className="flex-grow border-t border-gray-300"></div>
          <span className="flex-shrink mx-4 text-gray-500">hoc</span>
          <div className="flex-grow border-t border-gray-300"></div>
        </div>

        {/* Phn 2: Chn Vt phm (nu khng chn tnh nng) */}
        <div className={capability ? 'opacity-50 pointer-events-none' : ''}>
          <label className="block text-sm font-bold text-gray-700 mb-2">Chn Vt phm (Mn hc / Kha hc)</label>
          <div className="grid grid-cols-2 gap-4">
            <select 
              onChange={(e) => addToCart('subject', e.target.value)}
              className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:border-blue-500 focus:outline-none"
              disabled={!!capability}
            >
              <option value="">-- Thm Mn hc --</option>
              {subjects.map(s => (
                <option key={s.id} value={s.id} disabled={cart.subjects.includes(s.id)}>
                  {s.name}
                </option>
              ))}
            </select>
            <select 
              onChange={(e) => addToCart('course', e.target.value)}
              className="w-full px-4 py-3 border-2 border-gray-300 rounded-xl focus:border-blue-500 focus:outline-none"
              disabled={!!capability}
            >
              <option value="">-- Thm Kha hc --</option>
              {courses.map(c => (
                <option key={c.id} value={c.id} disabled={cart.courses.includes(c.id)}>
                  {c.name}
                </option>
              ))}
            </select>
          </div>
          
          {/* Hin th mini-cart */}
          <div className="mt-4 space-y-2">
            {cart.subjects.map(id => {
              const item = subjects.find(s => s.id === id);
              return (
                <div key={id} className="flex justify-between items-center bg-gray-50 p-2 rounded-lg">
                  <span className="text-sm font-semibold">{item?.name} (Mn)</span>
                  <button type="button" onClick={() => removeFromCart('subject', id)}><X size={16} className="text-red-500" /></button>
                </div>
              );
            })}
            {cart.courses.map(id => {
              const item = courses.find(c => c.id === id);
              return (
                <div key={id} className="flex justify-between items-center bg-gray-50 p-2 rounded-lg">
                  <span className="text-sm font-semibold">{item?.name} (Kha)</span>
                  <button type="button" onClick={() => removeFromCart('course', id)}><X size={16} className="text-red-500" /></button>
                </div>
              );
            })}
          </div>
        </div>
        
        <button
          type="submit"
          disabled={loading}
          className="w-full bg-gradient-to-r from-purple-600 to-blue-600 text-white font-bold py-4 rounded-xl hover:shadow-xl transition disabled:opacity-50"
        >
          {loading ? <Loader2 className="animate-spin" /> : 'To Key'}
        </button>
      </form>
    </div>
  );
};

//  MI: Component Qun l n hng
const OrderManager = ({ orders, users }) => {
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState('');

  //   CP NHT: handleGenerateKey (dng fetch)
  const handleGenerateKey = async (order) => {
    setLoading(true);
    setError('');
    try {
      // 1. Ly token ca Admin
      const user = auth.currentUser;
      if (!user) throw new Error("Admin cha ng nhp");
      const token = await user.getIdToken();
      
      const payload = {
        status: "new",
        cartToUnlock: order.cart,
        orderId: order.id
      };
      
      // 2. Gi API Vercel
      const response = await fetch(`${VERCEL_API_URL}/api/createAccessKey`, {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json',
          'Authorization': `Bearer ${token}`
        },
        body: JSON.stringify(payload)
      });
      
      const result = await response.json();
      if (!response.ok) {
        throw new Error(result.message || "Li khng xc nh");
      }
      
      const key = result.key;
      alert(`To Key thnh cng: ${key}\n\nHy gi Key ny cho ${order.userName}.`);
      
      // 3. Cp nht Firestore (Admin c quyn lm vic ny)
      await updateDoc(doc(db, 'orders', order.id), {
        status: 'processed',
        generatedKey: key
      });
      
    } catch (err) {
      console.error(err);
      setError(err.message);
      alert("Li khi to key: " + err.message);
    } finally {
      setLoading(false);
    }
  };

  return (
    <div className="bg-white rounded-2xl shadow-lg overflow-hidden">
      {error && <div className="p-4 bg-red-100 text-red-700">{error}</div>}
      <table className="w-full min-w-lg">
        <thead className="bg-gray-50">
          <tr>
            <th className="px-6 py-4 text-left text-xs font-bold text-gray-600 uppercase tracking-wider">Ngi dng</th>
            <th className="px-6 py-4 text-left text-xs font-bold text-gray-600 uppercase tracking-wider">Ni dung</th>
            <th className="px-6 py-4 text-left text-xs font-bold text-gray-600 uppercase tracking-wider">Trng thi</th>
            <th className="px-6 py-4 text-right text-xs font-bold text-gray-600 uppercase tracking-wider">Hnh ng</th>
          </tr>
        </thead>
        <tbody className="divide-y divide-gray-200">
          {orders.map(order => (
            <tr key={order.id}>
              <td className="px-6 py-4 whitespace-nowrap">
                <p className="font-semibold text-gray-800">{order.userName}</p>
                <p className="text-sm text-gray-500">{order.userId}</p>
              </td>
              <td className="px-6 py-4">
                <p className="text-sm">Mn: {order.cart.subjects.join(', ') || 'Khng c'}</p>
                <p className="text-sm">Kha: {order.cart.courses.join(', ') || 'Khng c'}</p>
              </td>
              <td className="px-6 py-4 whitespace-nowrap">
                {order.status === 'processed' ? (
                  <span className="px-3 py-1 rounded-full text-xs font-bold bg-green-100 text-green-700">
                     x l
                  </span>
                ) : (
                  <span className="px-3 py-1 rounded-full text-xs font-bold bg-yellow-100 text-yellow-700">
                    ang ch
                  </span>
                )}
              </td>
              <td className="px-6 py-4 whitespace-nowrap text-right space-x-2">
                {order.status !== 'processed' && (
                  <button
                    onClick={() => handleGenerateKey(order)}
                    disabled={loading}
                    className="bg-blue-100 text-blue-700 px-3 py-2 rounded-lg hover:bg-blue-200 transition text-sm disabled:opacity-50"
                  >
                    To Key
                  </button>
                )}
                {order.generatedKey && (
                   <span className="text-xs text-gray-500 font-mono">{order.generatedKey}</span>
                )}
              </td>
            </tr>
          ))}
        </tbody>
      </table>
    </div>
  );
};

// Main Admin Dashboard
const AdminDashboard = ({ user, onLogout }) => {
  const { role } = useContext(AppContext);
  const { subjects, courses, quizzes, loading: loadingData } = useContext(DataContext);
  //  THAY I: Ly thm 'orders' t hook
  const { users, transactions, orders, loading: loadingAdmin } = useAdminData(role);
  
  const [view, setView] = useState('users');
  
  //  THAY I: Cp nht Tabs
  const adminTabs = [
    { key: 'users', label: 'Ngi dng', icon: Users },
    { key: 'orders', label: 'n hng', icon: Package }, // Mi
    { key: 'create-key', label: 'To Key', icon: Sparkles }, // Mi
    { key: 'grant', label: 'Cp Key Trc tip', icon: Key }, // Sa tn
    { key: 'subjects', label: 'Mn hc', icon: BookOpen },
    { key: 'courses', label: 'Kha hc', icon: Package },
    { key: 'quizzes', label: 'Bi tp', icon: CheckCircle2 },
    { key: 'transactions', label: 'Giao dch (Log)', icon: BarChart3 }, // Sa tn
  ];

  const handleSave = async (data, id) => {
    const collectionName = view; // 'subjects', 'courses', 'quizzes'
    try {
      if (id) {
        // Update
        const docRef = doc(db, collectionName, id);
        await updateDoc(docRef, data);
      } else {
        // Create
        const collRef = collection(db, collectionName);
        if (collectionName === 'quizzes') {
          data.createdBy = auth.currentUser.uid; // Gn admin l ngi to
        }
        await addDoc(collRef, data);
      }
    } catch (err) {
      console.error(`Li khi lu ${collectionName}:`, err);
      alert(`Li: ${err.message}`);
    }
  };
  
  const handleDelete = async (id) => {
    const collectionName = view;
    if (!window.confirm(`Bn c chc mun xa ${collectionName} vi ID: ${id}?`)) return;
    try {
      const docRef = doc(db, collectionName, id);
      await deleteDoc(docRef);
      // TODO: Xa tham chiu (v d: xa quizId khi subjects)
      // Tm thi b qua  n gin (nhng TeacherDashboard C logic ny)
      // Cn ng b ha logic ny, l tng nht l dng Cloud Function
    } catch (err) {
      console.error(`Li khi xa ${collectionName}:`, err);
      alert(`Li: ${err.message}`);
    }
  };
  
  const renderView = () => {
    if (loadingData || loadingAdmin) {
      return <div className="text-center py-20"><Loader2 className="animate-spin mx-auto text-gray-400" size={48} /></div>;
    }
    
    switch(view) {
      case 'users':
        return <UserManager users={users} />;
      //  MI: Thm view cho 'orders' v 'create-key'
      case 'orders':
        return <OrderManager orders={orders} users={users} />;
      case 'create-key':
        return <ManualKeyGenerator subjects={subjects} courses={courses} />;
      case 'subjects':
        return <ContentManager type="subjects" items={subjects} onSave={handleSave} onDelete={handleDelete} />;
      case 'courses':
        return <ContentManager type="courses" items={courses} onSave={handleSave} onDelete={handleDelete} />;
      case 'quizzes':
        return <ContentManager type="quizzes" items={quizzes} onSave={handleSave} onDelete={handleDelete} />;
      case 'grant':
        return <ManualKeyGrant users={users} subjects={subjects} courses={courses} />;
      case 'transactions':
        return (
          <div className="bg-white rounded-2xl shadow-lg p-6">
            <h3 className="text-xl font-bold mb-6">Lch s Giao dch</h3>
            {/* n gin ha, ch hin th JSON */}
            <pre className="bg-gray-100 p-4 rounded-lg text-sm overflow-x-auto">
              {JSON.stringify(transactions, null, 2)}
            </pre>
          </div>
        );
      default:
        return null;
    }
  };

  return (
    <div className="min-h-screen bg-gray-100">
      <div className="bg-gradient-to-r from-red-600 to-purple-700 text-white shadow-lg">
        <div className="max-w-7xl mx-auto px-6 py-6">
          <div className="flex justify-between items-center">
            <div>
              <h1 className="text-3xl font-bold"> {user.hoTen}</h1>
              <p className="text-red-100 mt-1">Qun tr vin (Admin)</p>
            </div>
            <button
              onClick={onLogout}
              className="flex items-center gap-2 bg-white/20 hover:bg-white/30 px-6 py-3 rounded-xl transition"
            >
              <LogOut size={20} />
              ng xut
            </button>
          </div>
        </div>
      </div>

      <div className="bg-white shadow-sm border-b sticky top-0 z-10">
        <div className="max-w-7xl mx-auto px-6">
          <div className="flex gap-1 py-4 overflow-x-auto">
            {adminTabs.map(tab => (
              <button
                key={tab.key}
                onClick={() => setView(tab.key)}
                className={`px-5 py-3 rounded-xl font-semibold transition flex items-center gap-2 ${
                  view === tab.key
                    ? 'bg-gradient-to-r from-blue-600 to-purple-600 text-white'
                    : 'bg-gray-100 text-gray-700 hover:bg-gray-200'
                }`}
              >
                <tab.icon size={20} /> {tab.label}
              </button>
            ))}
          </div>
        </div>
      </div>
      
      <div className="max-w-7xl mx-auto px-6 py-12">
        {renderView()}
      </div>
    </div>
  );
};


