const AppRouter = () => {
  const { 
    authUser, 
    currentUser, 
    role, 
    isLoading, 
    needsOnboarding, 
    handleSignOut,
    setOnboardingCompleted // Ly hm ny t context
  } = useContext(AppContext);
  
  // Ti data  y, SAU KHI AppContext  sn sng
  const dataState = usePublicData(); 

  if (isLoading || dataState.loading) {
    return <GlobalLoader message="ang ti d liu..." />;
  }

  // Cung cp DataContext cho cc component con (Dashboards, v.v.)
  return (
    <DataContext.Provider value={dataState}>
      {!authUser ? (
        <LoginPage />
      ) : needsOnboarding ? (
        <OnboardingForm user={authUser} onComplete={setOnboardingCompleted} />
      ) : !currentUser ? (
        // Trng hp l:  auth, khng cn onboarding, nhng data user vn null
        // (C th do li Firestore)
        <GlobalLoader message="Li khi ti d liu ngi dng..." />
      ) : role === 'admin' ? (
        <AdminDashboard user={currentUser} onLogout={handleSignOut} />
      ) : role === 'teacher' ? (
        <TeacherDashboard user={currentUser} onLogout={handleSignOut} />
      ) : (
        <StudentDashboard user={currentUser} onLogout={handleSignOut} />
      )}
    </DataContext.Provider>
  );
};


// =====================================================
// MAIN APP
// =====================================================
export default function ELearningSystem() {
  const authState = useAuth();
  
  // X l logic xc nhn ng nhp (session conflict)
  const { sessionConflict, proceedToLogin, handleSignOut, kicked } = authState;

  const onConfirmLogin = () => {
    if (sessionConflict) {
      proceedToLogin(sessionConflict.authUser, sessionConflict.role);
    }
  };

  const onCancelLogin = () => {
    handleSignOut(); // ng xut ngi dng khi thit b ny
  };
  
  return (
    <AppContext.Provider value={authState}>
        {kicked && <KickedModal />}

        {sessionConflict && (
          <ConfirmLoginModal 
            onConfirm={onConfirmLogin}
            onCancel={onCancelLogin}
          />
        )}
        
        {!authState.isAuthReady ? (
          <GlobalLoader message="ang kt ni..." />
        ) : (
          <AppRouter />
        )}
    </AppContext.Provider>
  );
}

